:root {
  --font-inter: "Inter", sans-serif;
}

:root {
  --white: #ffffff;
  --bg-gray: hsl(0deg, 0%, 90%);
  --light-gray: hsl(0deg, 0%, 38%);
  --dark: hsl(0deg, 0%, 26%);
  --darker: hsl(0deg, 0%, 13%);
}

:root {
  --h1: 400 3.75rem Inter;
  --h2: 400 2rem Inter;
  --h3: 400 1.5rem Inter;
  --h4: 400 0.875rem/1.5rem Inter;
  --subTitle: 400 1.25rem Inter;
  --list: 400 1rem/1.5rem Inter;
  --body: 400 1rem/1.5rem Inter;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font: var(--h2);
  margin-bottom: 0.1875rem;
}
@media (min-width: 40em) {
  h1 {
    font: var(--h1);
  }
}

h2 {
  font: var(--h2);
}

p {
  font: var(--body);
  color: var(--dark);
}

a,
a:visited,
a:active {
  text-decoration: none;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background: var(--white);
}

.hero {
  margin-block-end: 2.5rem;
}
.hero .hero__title {
  margin-block-end: 1rem;
}

.index {
  margin-block-end: 2.5rem;
}
.index .navigation_title {
  text-transform: uppercase;
  font: var(--h4);
}
.index .index-details {
  border-left: 4px solid #c4c4c4;
  padding-inline: 1rem;
}
.index .index-details summary {
  font: var(--h4);
  font-weight: bold;
  color: var(--light-gray);
}
.index .index-details details ol li {
  margin-block: 1rem;
}
.index ol > li > a {
  color: var(--light-gray);
}

.container {
  margin-block: 2.5rem;
  margin-inline: 1rem;
}
@media (min-width: 40em) {
  .container {
    margin-block: 5rem;
    margin-inline: 12rem;
  }
}
.container section {
  width: 100%;
}
.container p {
  width: 100%;
}

.setup .setup__element {
  margin-block: 2.5rem;
}
.setup .setup__element :last-child {
  margin-block-end: 0px;
}
.setup .setup__element .element_title {
  font: var(--subTitle);
  font-size: 1.25rem;
  color: var(--dark);
}

.concepts h3 {
  color: var(--dark);
  margin-block-end: 1rem;
}
.concepts .post-element {
  color: var(--dark);
  margin-block: 1.5rem;
}
.concepts .post-element .html-code {
  background-color: var(--bg-gray);
  padding-block: 16px;
  padding-inline: 16px;
}
.concepts .post-element .html-code pre {
  margin: 0;
}

.box-model ol {
  padding-inline: 16px;
}
.box-model ul {
  padding-inline: 16px;
}
.box-model li {
  font: var(--list);
  color: var(--dark);
  margin-block-end: 16px;
}

.post {
  margin-block-end: 2.5rem;
}
.post .post-title {
  width: 100%;
  font: var(--h3);
  color: var(--darker);
}
.post .element_title {
  font: var(--subTitle);
  font-size: 1.25rem;
  color: var(--dark);
}
.post .post-img {
  width: 328px;
}
@media (min-width: 40em) {
  .post .post-img {
    width: 966px;
    height: 500px;
  }
}

.commment {
  border-left: 4px solid #212121;
  padding-inline-start: 1em;
  margin-block-end: 2.5rem;
}
.commment p {
  color: var(--dark);
}
.commment span {
  color: var(--light-gray);
}

.contact-info .email-link {
  color: black;
  cursor: pointer;
}

.subcription .form-title {
  font: var(--subTitle);
  color: var(--dark);
}
.subcription .input,
.subcription .button {
  font: var(--body);
  border-radius: 90px;
  padding-block: 12px;
  padding-inline: 16px;
  margin-block-end: 16px;
}
.subcription .button {
  background-color: black;
  color: var(--white);
}

.galery img {
  width: 328px;
}
@media (min-width: 40em) {
  .galery img {
    width: 966px;
    height: 500px;
  }
}

.footer {
  color: var(--white);
  text-align: center;
  background-color: #22252a;
  padding-inline: 2.625rem;
  padding-block-start: 3rem;
  padding-block-end: 3.625rem;
}
.footer .copyright {
  font: var(--h3);
  margin-block-end: 16px;
}
.footer .social-links {
  margin-inline: 16px;
}
.footer .social-links a {
  margin-inline: 16px;
}
.footer .social-links a i {
  font-size: 1.25rem;
  color: var(--white);
}